body {
  display: flex;
  justify-content: center;
}
.Game {
  margin: 20px;
  display: grid;
  grid-template-columns: 100px 100px 100px;
  grid-template-rows:  100px 100px 100px;
}
.Square {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90px;
  height: 90px;
  border: 1px solid black;
  font-size: 40px;
  font-weight: bold;
  color: green;
  outline: none;
}
.Square:hover {
  background: rgb(245, 210, 210);
}
.win {
  position: absolute;
  font-size: 48px;
  margin: auto;
  left: 100px;
  top: 100px;
  padding: 5px;
  border-radius: 20px;
  background: rgb(229, 245, 2);
}
#root {
  position: relative;
}